import type {
  HttpRequestOrHttpRequestFactory,
  HttpResponse,
  RequestOverrides,
} from '@wix/fe-essentials/http-client';
import { HttpClientMock } from '@wix/fe-essentials/http-client/testkit/client';
import { ErrorHandler } from '../error-handler/ErrorHandler';

export class ErrorHandlerHttpClientMock {
  readonly httpClient;
  readonly errorHandler;

  constructor(httpClient: HttpClientMock, errorHandler: ErrorHandler) {
    this.httpClient = httpClient;
    this.errorHandler = errorHandler;
  }

  async request<T>(
    req: HttpRequestOrHttpRequestFactory<T>,
    overrides?: RequestOverrides,
  ): Promise<HttpResponse<T>> {
    try {
      return await this.httpClient.request(req, overrides);
    } catch (e) {
      this.errorHandler.handleError(e);
      throw e;
    }
  }
}
